import React from "react";
import Button from "../../components/controls/inputs/Button";
import { Grid, Paper } from "@mui/material";
import { makeStyles } from "@mui/styles";

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(2.9),
    padding: theme.spacing(3),
  },
}));

export default function LinkToPages() {
  const classes = useStyles();
  return (
    <Paper className={classes.pageContent} style={{ overflowX: "auto" }}>
      <Grid container spacing={0}>
        <Grid item>
          <a href="/ContractCreate">
            <Button text=" " type="link" />
          </a>
        </Grid>
        <Grid item>
          <a href="/Contracts">
            <Button text=" " type="link" />
          </a>
        </Grid>
        <Grid item>
          <a href="/ContentService">
            <Button text=" " type="link" />
          </a>
        </Grid>
        <Grid item>
          <a href="/ContractCreate">
            <Button text=" " type="link" />
          </a>
        </Grid>
        <Grid item>
          <a href="/ContractCreate">
            <Button text="/" type="link" />
          </a>
        </Grid>
      </Grid>
    </Paper>
  );
}
