import { IconButton, InputAdornment } from "@mui/material";
import React from "react";
import CloseIcon from "@mui/icons-material/Close";
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import TableCell from "@mui/material/TableCell";
import TableRow from "@mui/material/TableRow";
import Select from "../../../components/controls/inputs/Select";
import Input from "../../../components/controls/inputs/Input";
import DatePicker from "../../../components/controls/DatePicker/DatePicker";
import { makeStyles } from "@mui/styles";

const useStyles = makeStyles((theme) => ({
  tblCell: {
    minWidth:"30%"
  },
}));

const options = [
  {
    id: 0,
    title: "",
  },
  {
    id: 1,
    title: "",
  },
  {
    id: 2,
    title: "",
  },
];

export default function TableRows(props) {
  const classes = useStyles();
  const { rowsData, deleteTableRows, handleChange } = props;
  return rowsData.map((data, index) => {
    const {
      ContractName,
      Count,
      SinglePrice,
      SumPrice,
      ContractDate,
      ProfitEmployer,
    } = data;
    return (
      <TableRow key={index}>
        <TableCell>{index + 1}</TableCell>

        <TableCell className={classes.tblCell} style={{width:"25%",textAlign:"right" }}>
          <Select
            name="ContractName"
            value={ContractName}
            options={options}
            size="small"
            onChange={(event) => handleChange(index, event)}
          />
        </TableCell>
        <TableCell style={{width:"7%",textAlign:"right" }}>
          <Input
            variant="outlined"
            name="Count"
            value={Count}
            size="small"
            onChange={(event) => handleChange(index, event)}
          />
        </TableCell>
        <TableCell style={{width: '20%',textAlign:"right" }}>
          <Input
            variant="outlined"
            name="SinglePrice"
            value={SinglePrice}
            size="small"
            onChange={(event) => handleChange(index, event)}
            InputProps={{
              inputMode: "numeric",
              pattern: "[,]*",
              inputProps: { min: 0 },
              endAdornment: (
                <InputAdornment style={{ marginLeft: "5px" }} position="start">
                  
                </InputAdornment>
              ),
            }}
          />
        </TableCell>
        <TableCell style={{width: '20%',textAlign:"right" }}>
          <Input
            variant="outlined"
            name="SumPrice"
            value={SumPrice}
            size="small"
            onChange={(event) => handleChange(index, event)}
            InputProps={{
              inputMode: "numeric",
              pattern: "[,]*",
              inputProps: { min: 0 },
              endAdornment: (
                <InputAdornment style={{ marginLeft: "5px" }} position="start">
                  
                </InputAdornment>
              ),
            }}
          />
        </TableCell>
        <TableCell style={{width: '60%',textAlign:"right" }}>
          <DatePicker
            name="ContractDate"
            value={ContractDate}
            size="small"
            onChange={(event) => handleChange(index, event)}
          />
        </TableCell>
        <TableCell style={{width: '20%',textAlign:"right" }}>
          <Input
            variant="outlined"
            name="ProfitEmployer"
            value={ProfitEmployer}
            size="small"
            onChange={(event) => handleChange(index, event)}
            InputProps={{
              inputMode: "numeric",
              pattern: "[,]*",
              inputProps: { min: 0 },
              endAdornment: (
                <InputAdornment style={{ marginLeft: "5px" }} position="start">
                  
                </InputAdornment>
              ),
            }}
          />
        </TableCell>
        <TableCell style={{textAlign:"right" }}>
        <IconButton
            size="small"
          >
            <MonetizationOnIcon />
          </IconButton>
        </TableCell>
        <TableCell>
          <IconButton
            size="small"
            color="error"
            onClick={() => deleteTableRows(index)}
          >
            <CloseIcon />
          </IconButton>
        </TableCell>
      </TableRow>
    );
  });
}
