import * as React from "react";
import { useNavigate } from "react-router-dom";
import Avatar from "@mui/material/Avatar";
import Button from "../../components/controls/inputs/Button";
import Input from "../../components/controls/inputs/Input";
import Checkbox from "../../components/controls/inputs/CheckBox";
import FormControlLabel from "@mui/material/FormControlLabel";
import Link from "@mui/material/Link";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import { useForm } from "../../components/controls/use/useForm";

function Copyright(props) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"Copyright  "}
      <Link color="inherit" href="">
        Studio Rasool Akhavan
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}
const initialValues = {
  id: 0,
  Username: "",
  Password: "",
  Remember: false,
};

export default function Login() {
  const history = useNavigate();
  const { values, handleInputChange } = useForm(initialValues);
  const handleSubmit = (event) => {
    event.preventDefault();
    history("/Home");
  };

  return (
    <Grid container component="main" sx={{ height: "100vh",direction:'ltr' }}>
      <Grid
        item
        xs={false}
        sm={4}
        md={7}
        sx={{
          backgroundImage: `url(${require("../../img/img.jpg")})`,
          backgroundRepeat: "no-repeat",
          backgroundColor: (t) =>
            t.palette.mode === "light"
              ? t.palette.grey[50]
              : t.palette.grey[900],
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
      />
      <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
        <Box
          sx={{
            my: 8,
            mx: 4,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            
          </Typography>
          <Box
            component="form"
            noValidate
            onSubmit={handleSubmit}
            sx={{ mt: 1 }}
          >
            <Input
              margin="normal"
              required
              fullWidth
              label=" "
              name="Username"
              autoComplete="Username"
              value={values.Username}
              onChange={handleInputChange}
              autoFocus
            />
            <Input
              margin="normal"
              required
              fullWidth
              name="Password"
              label=""
              type="Password"
              autoComplete="current-password"
              value={values.Password}
              onChange={handleInputChange}
            />
            <FormControlLabel
              control={
                <Checkbox
                  name="Remember"
                  color="primary"
                  value={values.Remember}
                  onChange={handleInputChange}
                />
              }
              label="    "
            />
            <Button
              type="submit"
              text=""
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            />
            <Grid container>
              <Grid item xs></Grid>
              <Grid item>
                <Typography href="#" variant="body2">
                  {"         "}
                </Typography>
              </Grid>
            </Grid>
            <Copyright sx={{ mt: 5 }} />
          </Box>
        </Box>
      </Grid>
    </Grid>
  );
}
