import React from "react";
import { IconButton, Grid, InputAdornment } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import Input from "../../../../components/controls/inputs/Input";

export default function AddEmps(props) {
  const { employeeData, deleteEmployeeRows, handleChangeEmp } = props;
  return employeeData.map((data, index) => {
    const { EmpName, EmpSalary } = data;
    return (
      <Grid container key={index}>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=" "
            name="EmpName"
            value={EmpName}
            onChange={(event) => handleChangeEmp(index, event)}
          />
        </Grid>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=" "
            name="EmpSalary"
            value={EmpSalary}
            onChange={(event) => handleChangeEmp(index, event)}
            InputLabelProps={{ shrink: true }}
            InputProps={{
              inputMode: "numeric",
              pattern: "[,]*",
              inputProps: { min: 0 },
              endAdornment: (
                <InputAdornment style={{ marginLeft: "5px" }} position="start">
                  
                </InputAdornment>
              ),
            }}
          />
          <IconButton
            style={{ marginTop: "20px" }}
            color="error"
            onClick={() => deleteEmployeeRows(index)}
          >
            <CloseIcon />
          </IconButton>
        </Grid>
      </Grid>
    );
  });
}
