import React, { useState } from "react";
import { Grid, InputAdornment } from "@mui/material";
import Button from "../../components/controls/inputs/Button";
import Input from "../../components/controls/inputs/Input";
import { Form } from "../../components/controls/use/useForm";
import DatePicker from "../../components/controls/DatePicker/DatePicker";
import Select from "../../components/controls/inputs/Select";

const options = [
  {
    id: 0,
    title: "",
  },
  {
    id: 4,
    title: "",
  },
  {
    id: 1,
    title: "",
  },
  {
    id: 2,
    title: "",
  },
  {
    id: 3,
    title: "",
  },
];
const InitialValue = {
  id: 0,
  ClientName: "",
  ClientLastName: "",
  ClientPhoneNumber: "",
  SumPrice: "",
  ContractDate: null,
  ContractName: 0,
};

export default function SearchContracts() {
  const [search, setSearch] = useState(InitialValue);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(search);
  };

  const handleChangeInputs = (evnt) => {
    const { name, value } = evnt.target;
    setSearch({
        ...search,
        [name]: value,
      });
  };
  return (
    <Form onSubmit={handleSubmit}>
      <Grid container>
        <Grid item xs={3}>
          <Input
            variant="outlined"
            label=" "
            name="ClientName"
            value={search.ClientName}
            onChange={(event) => handleChangeInputs(event)}
          />
        </Grid>
        <Grid item xs={3}>
          <Input
            variant="outlined"
            label=" "
            name="ClientLastName"
            value={search.ClientLastName}
            onChange={(event) => handleChangeInputs(event)}
          />
        </Grid>
        <Grid item xs={3}>
          <Input
            variant="outlined"
            label="  "
            name="ClientPhoneNumber"
            value={search.ClientPhoneNumber}
            onChange={(event) => handleChangeInputs(event)}
          />
        </Grid>
        <Grid item xs={3}>
          <Input
            variant="outlined"
            name="SumPrice"
            label=" "
            value={search.SumPrice}
            onChange={(event) => handleChangeInputs(event)}
            InputLabelProps={{ shrink: true }}
            InputProps={{
              inputMode: "numeric",
              pattern: "[,]*",
              inputProps: { min: 0 },
              endAdornment: (
                <InputAdornment style={{ marginLeft: "5px" }} position="start">
                  
                </InputAdornment>
              ),
            }}
          />
        </Grid>
        <Grid item xs={3}>
          <DatePicker
            name="ContractDate"
            value={search.ContractDate}
            onChange={(event) => handleChangeInputs(event)}
          />
        </Grid>
        <Grid item xs={3}>
          <Select
            name="ContractName"
            value={search.ContractName}
            options={options}
            onChange={(event) => handleChangeInputs(event)}
          />
        </Grid>
        <Grid item xs={12}>
          <Button text="" type="submit" />
        </Grid>
      </Grid>
    </Form>
  );
}
