import React from "react";
import { Route, Routes } from "react-router-dom";
import Login from "../pages/Login/Login";
import Home from "../pages/Home/Home";
import LinkToPages from "../pages/Home/LinkToPages";
import Container from "../pages/Menu/Container";
import ContentContract from "../pages/CreateContract/ContentContract";
import Contracts from "../pages/Contract/Contracts";
import ContentService from "../pages/Services/CreateService/ContentService";
import Services from "../pages/Services/Service/Services";

export default function Router() {
  return (
    <Routes>
      <Route path="/" element={<Container />}>
        <Route exact path="/Link" element={<LinkToPages />} />
        <Route path="/Home" element={<Home />} />
        <Route path="/ContractCreate" element={<ContentContract />} />
        <Route path="/Contracts" element={<Contracts />} />
        <Route path="/ContentService" element={<ContentService />} />
        <Route path="/Services" element={<Services />} />
      </Route>
      <Route path="/Login" element={<Login />} />
    </Routes>
  );
}
