import React from "react";
import { Grid, InputAdornment } from "@mui/material";
import Input from "../../../../components/controls/inputs/Input";

export default function AddService(props) {
  const { serviceData, handleChangeService } = props;
  return serviceData.map((data, index) => {
    const { ServiceName, ServicePay } = data;
    return (
      <Grid container key={index}>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=" "
            name="ServiceName"
            value={ServiceName}
            onChange={(event) => handleChangeService(index, event)}
          />
        </Grid>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label="   "
            name="ServicePay"
            value={ServicePay}
            onChange={(event) => handleChangeService(index, event)}
            InputLabelProps={{ shrink: true }}
            InputProps={{
              inputMode: "numeric",
              pattern: "[,]*",
              inputProps: { min: 0 },
              endAdornment: (
                <InputAdornment style={{ marginLeft: "5px" }} position="start">
                  
                </InputAdornment>
              ),
            }}
          />
        </Grid>
      </Grid>
    );
  });
}
