import React, { useState } from "react";
import { Grid, IconButton, FormLabel } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
//import Input from "../../components/controls/inputs/Input";
import Button from "../../components/controls/inputs/Button";
import { Form } from "../../components/controls/use/useForm";
import AddDeleteTableRows from "./components/AddDeleteTableRows";
import AddInputs from "./components/AddInputs";
import AddDeletePay from "./components/AddDeletePay";

export default function Create() {
  const [rowsData, setRowsData] = useState([]);
  const [clientData, setClientData] = useState([]);
  const [payData, setPayData] = useState([
    { Amount: "", PayDate: "", PayMethod: "", PreOrIns: "" },
  ]);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(rowsData);
    console.log(clientData);
  };

  const addTableRows = () => {
    const rowsInput = {
      ContractName: "",
      Count: "",
      SinglePrice: "",
      SumPrice: "",
      ContractDate: "",
      ProfitEmployer: "",
      SalaryEmployee: "",
    };
    setRowsData([...rowsData, rowsInput]);
  };
  const deleteTableRows = (index) => {
    const rows = [...rowsData];
    rows.splice(index, 1);
    setRowsData(rows);
  };

  const addInputs = () => {
    const clientsInput = {
      ClientName: "",
      ClientLastName: "",
      ClientPhoneNumber: "",
    };
    setClientData([...clientData, clientsInput]);
  };
  const deleteInputs = (index) => {
    const clients = [...clientData];
    clients.splice(index, 1);
    setClientData(clients);
  };

  const addPays = () => {
    const paysInput = {
      Amount: "",
      PayDate: "",
      PayMethod: "",
      PreOrIns: "",
    };
    setPayData([...payData, paysInput]);
  };
  const deletePays = (index) => {
    const pays = [...payData];
    pays.splice(index, 1);
    setPayData(pays);
  };

  const handleChangePays = (index, evnt) => {
    const { name, value } = evnt.target;
    const rowsInput = [...payData];
    rowsInput[index][name] = value;
    setClientData(rowsInput);
  };

  const handleChangeInputs = (index, evnt) => {
    const { name, value } = evnt.target;
    const rowsInput = [...clientData];
    rowsInput[index][name] = value;
    setClientData(rowsInput);
  };
  const handleChange = (index, evnt) => {
    const { name, value } = evnt.target;
    const rowsInput = [...rowsData];
    rowsInput[index][name] = value;
    setRowsData(rowsInput);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Grid container>
        <Grid item xs={12}>
          <FormLabel> </FormLabel>
          <IconButton color="success" onClick={addInputs}>
            <AddIcon />
          </IconButton>
        </Grid>
        <Grid item xs={12} style={{marginTop:"5px",marginBottom:'8px'}}>
          <AddInputs
            clientData={clientData}
            deleteInputs={deleteInputs}
            addInputs={addInputs}
            handleChangeInputs={handleChangeInputs}
          />
        </Grid>
        <FormLabel> </FormLabel>
        <Grid item xs={12}>
          <AddDeleteTableRows
            rowsData={rowsData}
            deleteTableRows={deleteTableRows}
            addTableRows={addTableRows}
            handleChange={handleChange}
          />
        </Grid>
        <Grid item>
          <FormLabel></FormLabel>
          <IconButton color="success" onClick={addPays}>
            <AddIcon />
          </IconButton>
        </Grid>
        <Grid item xs={12}>
          <AddDeletePay
            payData={payData}
            deletePays={deletePays}
            handleChangePays={handleChangePays}
          />
        </Grid>
        <Grid item>
          <Button text="" type="submit" />
        </Grid>
      </Grid>
    </Form>
  );
}
