import React from "react";
import { IconButton, Grid } from "@mui/material";
import Input from "../../../components/controls/inputs/Input";
import CloseIcon from "@mui/icons-material/Close";

export default function AddInputs(props) {
  const { clientData, deleteInputs, handleChangeInputs } = props;
  return clientData.map((data, index) => {
    const { ClientName, ClientLastName, ClientPhoneNumber } = data;
    return (
      <Grid container key={index}>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=" "
            name="ClientName"
            value={ClientName}
            onChange={(event) => handleChangeInputs(index, event)}
          />
        </Grid>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=" "
            name="ClientLastName"
            value={ClientLastName}
            onChange={(event) => handleChangeInputs(index, event)}
          />
        </Grid>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=" "
            name="ClientPhoneNumber"
            value={ClientPhoneNumber}
            onChange={(event) => handleChangeInputs(index, event)}
          />
          <IconButton style={{marginTop:"20px"}}
            size="small"
            color="error"
            onClick={() => deleteInputs(index)}
          >
            <CloseIcon />
          </IconButton>
        </Grid>          
      </Grid>
    );
  });
}
