import { FormLabel, Grid, IconButton } from "@mui/material";
import React, { useState } from "react";
import { Form } from "../../../components/controls/use/useForm";
import AddIcon from "@mui/icons-material/Add";
import Button from "../../../components/controls/inputs/Button";
import AddEmps from "./components/AddEmps";
import AddService from "./components/AddService";
import AddCosts from "./components/AddCosts";

export default function CreateService() {
  const [serviceData, setServiceData] = useState([
    {
      ServiceName: "",
      ServicePay: "",
    },
  ]);
  const [employeeData, setEmployeeData] = useState([
    { EmpName: "", EmpSalary: "" },
  ]);
  const [costData, setCostData] = useState([{ CostName: "", CostAmount: "" }]);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(serviceData);
    console.log(employeeData);
    console.log(costData);
  };

  const addEmployeeRows = () => {
    const empsInput = {
      EmpName: "",
      EmpSalary: "",
    };
    setEmployeeData([...employeeData, empsInput]);
  };
  const deleteEmployeeRows = (index) => {
    const emps = [...employeeData];
    emps.splice(index, 1);
    setEmployeeData(emps);
  };

  const addCostRows = () => {
    const costsInput = {
      CostName: "",
      CostAmount: "",
    };
    setCostData([...costData, costsInput]);
  };
  const deleteCostRows = (index) => {
    const costs = [...costData];
    costs.splice(index, 1);
    setCostData(costs);
  };

  const handleChangeEmp = (index, evnt) => {
    const { name, value } = evnt.target;
    const empsInput = [...employeeData];
    empsInput[index][name] = value;
    setEmployeeData(empsInput);
  };
  const handleChangeCost = (index, evnt) => {
    const { name, value } = evnt.target;
    const costsInput = [...costData];
    costsInput[index][name] = value;
    setCostData(costsInput);
  };
  const handleChangeService = (index, evnt) => {
    const { name, value } = evnt.target;
    const serviceInput = [...serviceData];
    serviceInput[index][name] = value;
    setServiceData(serviceInput);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Grid container>
        <Grid item xs={10}>
          <Grid item>
            <FormLabel> </FormLabel>
            {/* <IconButton color="success" onClick={addInputs}>
            <AddIcon />
          </IconButton> */}
            <AddService
              serviceData={serviceData}
              handleChangeService={handleChangeService}
            />
          </Grid>
          <Grid item>
            <FormLabel> </FormLabel>
            <IconButton color="success" onClick={addEmployeeRows}>
              <AddIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <AddEmps
              employeeData={employeeData}
              deleteEmployeeRows={deleteEmployeeRows}
              handleChangeEmp={handleChangeEmp}
            />
          </Grid>
          <Grid item>
            <FormLabel>  </FormLabel>
            <IconButton color="success" onClick={addCostRows}>
              <AddIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <AddCosts
              costData={costData}
              deleteCostRows={deleteCostRows}
              handleChangeCost={handleChangeCost}
            />
          </Grid>
          <Grid item>
            <Button text="" type="submit" />
          </Grid>
        </Grid>
        <Grid item xs={2}>
          <Grid item  mb={2}>
            <FormLabel></FormLabel>
          </Grid>
          <Grid item> :</Grid>
          <Grid item>  :</Grid>
          <hr />
          <Grid item>:</Grid>
          <Grid item>:</Grid>
        </Grid>
      </Grid>
    </Form>
  );
}
