import React from "react";
import { IconButton, Grid, InputAdornment } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import Input from "../../../../components/controls/inputs/Input";

export default function AddCosts(props) {
  const { costData, deleteCostRows, handleChangeCost } = props;
  return costData.map((data, index) => {
    const { CostName, CostAmount } = data;
    return (
      <Grid container key={index}>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=" "
            name="CostName"
            value={CostName}
            onChange={(event) => handleChangeCost(index, event)}
          />
        </Grid>
        <Grid item xs={4}>
          <Input
            variant="outlined"
            label=""
            name="CostAmount"
            value={CostAmount}
            onChange={(event) => handleChangeCost(index, event)}
            InputLabelProps={{ shrink: true }}
            InputProps={{
              inputMode: "numeric",
              pattern: "[,]*",
              inputProps: { min: 0 },
              endAdornment: (
                <InputAdornment style={{ marginLeft: "5px" }} position="start">
                  
                </InputAdornment>
              ),
            }}
          />
          <IconButton
            style={{ marginTop: "20px" }}
            color="error"
            onClick={() => deleteCostRows(index)}
          >
            <CloseIcon />
          </IconButton>
        </Grid>
      </Grid>
    );
  });
}
