import {
  Divider,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import React from "react";
import { Link } from "react-router-dom";
import NavbarList from "./NavList";
import NavbarListAdmin from "./NavListAdmin";

export default function Navbar(props) {
  //   const { open, toogleOpenSearch, refFocus } = props;
  const { open } = props;
  return (
    <>
      {NavbarList.map((key, index) => (
        <ListItem
          key={key.desc}
          disablePadding
          sx={{ display: "block", direction: "rtl" }}
        >
          <Link to={key.link || "/"} style={{ textDecoration: 'none' }}>
            <ListItemButton
              sx={{
                minHeight: 48,
                justifyContent: open ? "initial" : "center",
                px: 2.5,
              }}
            >
              <ListItemIcon
                sx={{
                  minWidth: 0,
                  mr: open ? 3 : "auto",
                  justifyContent: "center",
                }}
              >
                <key.icon sx={{ fontSize: "20px", color: "#3c44b1" }} />
              </ListItemIcon>
              <ListItemText
                primary={key.desc}
                sx={{
                  opacity: open ? 1 : 0,
                  textAlign: open && "right",
                  paddingRight: open && "20px",
                }}
              />
            </ListItemButton>
          </Link>
        </ListItem>
      ))}
      <Divider variant="middle" light={true} />
      {NavbarListAdmin.map((key, index) => (
        <ListItem
          key={key.desc}
          disablePadding
          sx={{ display: "block", direction: "rtl" }}
        >
          <Link to={key.link || "/"} style={{ textDecoration: 'none' }}>
            <ListItemButton
              sx={{
                minHeight: 48,
                justifyContent: open ? "initial" : "center",
                px: 2.5,
              }}
            >
              <ListItemIcon
                sx={{
                  minWidth: 0,
                  mr: open ? 3 : "auto",
                  justifyContent: "center",
                }}
              >
                <key.icon sx={{ fontSize: "20px", color: "#3c44b1" }} />
              </ListItemIcon>
              <ListItemText
                primary={key.desc}
                sx={{
                  opacity: open ? 1 : 0,
                  textAlign: open && "right",
                  paddingRight: open && "20px",
                }}
              />
            </ListItemButton>
          </Link>
        </ListItem>
      ))}
    </>
  );
}
