import { InputLabel, TextField } from "@mui/material";
import React from "react";
import "./DatePicker.css";
import AdapterJalali from '@date-io/date-fns-jalali';
import { DatePicker as MuiDate } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';


export default function DatePicker(props) {
  const { name, label, value, onChange,size, ...other  } = props;

  const convertToDefaultEventObj = (name, value) => ({
    target: {
      name,
      value,
    },
  });
  return (
    <>
      <InputLabel>{label}</InputLabel>
      <LocalizationProvider dateAdapter={AdapterJalali}>
      <MuiDate
        mask="____/__/__"
        value={value}
        onChange={(date) =>
          onChange(convertToDefaultEventObj(name, date))
        }
        renderInput={(params) => <TextField size={size} {...params} />}
        {...other}
      />
    </LocalizationProvider>
      {/* <Calnder
        className="DatePicker"
        id={name}
        isGregorian={false}
        timePicker={false}
        value={value}
        onChange={(date) =>
          onChange(convertToDefaultEventObj(name, moment(date)))
        }
      /> */}
    </>
  );
}
