import React from "react";
import { IconButton, Grid, InputAdornment } from "@mui/material";
import Input from "../../../components/controls/inputs/Input";
import CloseIcon from "@mui/icons-material/Close";
import Select from "../../../components/controls/inputs/Select";
import DatePicker from "../../../components/controls/DatePicker/DatePicker";

const options = [
  {
    id: 0,
    title: "",
  },
  {
    id: 1,
    title: "",
  },
];
const PayOrInst = [
    {
      id: 0,
      title: " ",
    },
    {
      id: 1,
      title: "",
    },
  ];


export default function AddDeletePay(props) {
  const { payData, deletePays, handleChangePays } = props;
  return payData.map((data, index) => {
    const { PreOrIns,Amount, PayDate, PayMethod } = data;
    return (
      <Grid container key={index}>
          <Grid item xs={3}>
          <Select
            name="PreOrIns"
            value={PreOrIns}
            options={PayOrInst}
            onChange={(event) => handleChangePays(index, event)}
          />
        </Grid>
        <Grid item xs={3}>
          <Select
            name="PayMethod"
            value={PayMethod}
            options={options}
            onChange={(event) => handleChangePays(index, event)}
          />
        </Grid>
        <Grid item xs={3}>
          <Input
            variant="outlined"
            name="Amount"
            value={Amount}
            onChange={(event) => handleChangePays(index, event)}
            InputProps={{
                inputMode: "numeric",
                pattern: "[,]*",
                inputProps: { min: 0 },
                endAdornment: (
                  <InputAdornment style={{ marginLeft: "5px" }} position="start">
                    
                  </InputAdornment>
                ),
              }}
          />
        </Grid>
        <Grid item xs={3}>
          <DatePicker
            name="PayDate"
            value={PayDate}
            onChange={(event) => handleChangePays(index, event)}
          />
          <IconButton
            style={{ marginTop: "20px" }}
            color="error"
            onClick={() => deletePays(index)}
          >
            <CloseIcon />
          </IconButton>
        </Grid>
      </Grid>
    );
  });
}
