import React from "react";
import TableRows from "./TableRows";
import AddIcon from "@mui/icons-material/Add";
import { IconButton } from "@mui/material";
import { Paper } from "@mui/material";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import { makeStyles } from "@mui/styles";

const useStyles = makeStyles((theme) => ({
  tbl: {
    textAlign: "center",
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    padding:theme.spacing(2)
  },
  tblCell: {
    textAlign: "right !important",
  },
}));

export default function AddDeleteTableRows(props) {
  const classes = useStyles();
  const { rowsData, deleteTableRows, handleChange, addTableRows } = props;

  return (
    <TableContainer className={classes.tbl} component={Paper} elevation={2}>
      <Table className="table">
        <TableHead>
          <TableRow>
            <TableCell className={classes.tblCell}></TableCell>
            <TableCell className={classes.tblCell}> </TableCell>
            <TableCell className={classes.tblCell}></TableCell>
            <TableCell className={classes.tblCell}> </TableCell>
            <TableCell className={classes.tblCell}> </TableCell>
            <TableCell className={classes.tblCell}></TableCell>
            <TableCell className={classes.tblCell}> </TableCell>
            <TableCell className={classes.tblCell}> </TableCell>
            <TableCell>
              <IconButton color="success" onClick={addTableRows}>
                <AddIcon />
              </IconButton>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRows
            rowsData={rowsData}
            deleteTableRows={deleteTableRows}
            handleChange={handleChange}
          />
        </TableBody>
      </Table>
    </TableContainer>
  );
}
